import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../actions/datamaster_action";
import { ReanderField } from "../../components/helpers/field";
import { Loading } from "../../components/helpers/library";
import { ToastNotification } from "../../components/helpers/notification";
import { postData } from "../../config/axios";
class ProsesOpname extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loadingData: false,
    };
  }

  componentDidMount() {
    setTimeout(() => {
    document.getElementById("kode_barcode").focus()
    }, 100)
  }
  cekBarcode(e) {
    // console.log(e);
    clearTimeout(this.delayTimer);
    this.delayTimer = setTimeout(() => {
      this.setState({ loadingData: true });
      if (e === "") {
        this.setState({ loadingData: false, databarang: [] });
        return false;
      } let hasil = {
        kode_barcode: e,
      };
      postData("opname/add", hasil)
        .then((res) => {
          ToastNotification("success", "Data Berhasil Ditambahkan");
          console.log(res.data);
          this.setState({
            loadingData: false,
          });
          res.data.barang.forEach((item, index) => {
            this.props.change("kode_toko", item.kode_toko);
            this.props.change("kode_dept", item.kode_dept);
            this.props.change("nama_barang", item.nama_barang);
            this.props.change("nama_atribut", item.nama_atribut);
            this.props.change("kode_intern", item.kode_intern);
            this.props.change("berat", item.berat);
          });
          this.props.change("kode_barcode", "");
          // res.data.forEach(element => {
          //     console.log(element.kode_dept);
          // });
          document.getElementById("kode_barcode").focus()

        })
        .catch((err) => {
          this.setState({
            loadingData: false,
          });
          this.props.change("kode_toko", "");
          this.props.change("kode_dept", "");
          this.props.change("nama_barang", "");
          this.props.change("nama_atribut", "");
          this.props.change("kode_intetn", "");
          this.props.change("berat", "");
          ToastNotification("info", err.response.data);
          this.props.change("kode_barcode", "");
          document.getElementById("kode_barcode").focus()

        });
    }, 300)
  }
  batal() {
    this.props.dispatch(hideModal());
  }
  render() {
    return (
      //   <form
      //     onSubmit={this.props.handleSubmit}
      //     onKeyPress={(e) => {
      //       e.key === "Enter" && e.preventDefault();
      //     }}
      //     autoComplete="off"
      //   >
      <div className="row">
        <div className="col-12">
          <Field
            id="kode_barcode"
            name="kode_barcode"
            onChange={(e) => this.cekBarcode(e.target.value)}
            label="Kode Barcode"
            type="text"
            normalize={(value) => value.replace(/[^\d]/g, "")}
            placeholder="Silahkan Masukan Kode Barcode"
            component={ReanderField}
            maxLength="8"
          />
        </div>
        <div className="col-6">
          <Field
            name="kode_toko"
            label="Kode Baki"
            type="text"
            readOnly={true}
            placeholder="Silahkan Masukan Kode Baki"
            component={ReanderField}
          />
        </div>
        <div className="col-6">
          <Field
            name="kode_dept"
            type="text"
            readOnly={true}
            label="Kode Jenis"
            placeholder="Silahkan Masukan Kode Jenis"
            component={ReanderField}
          />
        </div>
        <div className="col-6">
          <Field
            name="kode_intern"
            type="text"
            readOnly={true}
            label="Kode Intern"
            placeholder="Silahkan Masukan Kode Intern"
            component={ReanderField}
          />
        </div>
        <div className="col-6">
          <Field
            name="berat"
            type="text"
            readOnly={true}
            label="Berat"
            placeholder="Silahkan Masukan Berat"
            component={ReanderField}
          />
        </div>
        <div className="col-6">
          <Field
            name="nama_barang"
            readOnly={true}
            type="text"
            label="Nama Barang"
            placeholder="Silahkan Masukan Nama Barang"
            component={ReanderField}
          />
        </div>
        <div className="col-6">
          <Field
            readOnly={true}
            type="text"
            name="nama_atribut"
            label="Nama Atribut"
            placeholder="Silahkan Masukan Nama Atribut"
            component={ReanderField}
          />
        </div>
        <div className="col-12">
          <button
            className="btn btn-primary btn-block"
            disabled={this.state.batal}
            type="button"
            onClick={() => this.batal()}
          >
            Kembali
          </button>
        </div>
        {this.state.loadingData ? (
          <>
            <Loading
              loading
              background="rgba(0, 0, 0, 0.35)"
              loaderColor="rgba(94, 147, 117, 1)"
            />
          </>
        ) : null}
      </div>
      //   </form>
    );
  }
}

ProsesOpname = reduxForm({
  form: "ProsesOpname",
  enableReinitialize: true,
})(ProsesOpname);
export default connect()(ProsesOpname);

