import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
class HeadStockOpname extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <button
              className="btn btn-primary btn-block"
              type="button"
              onClick={this.props.btnKosongkanDataOpname}
            >
              Kosongkan Data Opname
            </button>
          </div>
          <div className="col-4">
            <button
              onClick={this.props.lanjutOpname}
              className="btn btn-primary btn-block"
              type="button"
            >
              Lanjut Opname
            </button>
          </div>
          <div className="col-4">
            <button
              className="btn btn-primary btn-block"
              onClick={this.props.lihatstockopname}
              disabled={this.props.loadingLihatSTockOpname}
              type="button"
            >
              {this.props.loadingLihatSTockOpname ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Lihat Hasil
                  Opname
                </>
              ) : (
                "Lihat Hasil Opname"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

HeadStockOpname = reduxForm({
  form: "HeadStockOpname",
  enableReinitialize: true,
})(HeadStockOpname);
export default connect()(HeadStockOpname);

