import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm, reset } from "redux-form";
import { hideModal, showModal } from "../../actions/datamaster_action.jsx";
import ModalGlobal from "../../components/helpers/ModalGlobal.jsx";
import { ToastNotification } from "../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../components/panel/panel.jsx";
import { deleteData, getData, postData } from "../../config/axios.jsx";
import HeadStockOpname from "./HeadStockOpname.jsx";
import KosongKanDataOpname from "./KosongKanDataOpname.jsx";
import ProsesOpname from "./ProsesOpname.jsx";
import TabelLihatBarangTidakKetemu from "./tabel/TabelLihatBarangTidakKetemu.jsx";
import TabelListBarangKetemu from "./tabel/TabelListBarangKetemu.jsx";

class FormStockOpname extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      FormModal: "",
      barangHilang : [],
      barangKetemu : [],
      isLoadingBakiOpname: false,
      loadingLihatSTockOpname: false,
      tbl_barang_tidak_ketemu: [
        {
          dataField: "kode_toko",
          text: "Kode Baki ",
          footer: "",
          footerAttrs: {
            colSpan: "3",
          },
        },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "kode_group",
          text: "Kategori",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
          footer: "Total Qty",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: (rowcontent, row) => {
            return localStorage.getItem('total_qty_brg_hilang') === null || undefined ? 0 : parseInt(localStorage.getItem('total_qty_brg_hilang'))
          },
        },
        {
          dataField: "nama_atribut",
          text: "Nama Atribut",
          footer: "Total Berat",
        },
        {
          dataField: "berat",
          text: "Berat",
          footer: (rowcontent, row) => {
            return localStorage.getItem('total_berat_brg_hilang') === null || undefined ? 0 : parseFloat(localStorage.getItem('total_berat_brg_hilang')).toFixed(3)
          },
        },
      ],
      tbl_barang_ketemu: [
        {
          dataField: "kode_toko",
          text: "Kode Baki ",
          footer: "",
          footerAttrs: {
            colSpan: "3",
          },
        },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "kode_group",
          text: "Kategori",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
          footer: "Total Qty",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: (rowcontent, row) => {
            return localStorage.getItem('total_qty_brg_ketemu') === null || undefined ? 0 : parseInt(localStorage.getItem('total_qty_brg_ketemu'))
          },
        },
        {
          dataField: "nama_atribut",
          text: "Nama Atribut",
          footer: "Total Berat",
        },
        {
          dataField: "berat",
          text: "Berat",
          footer: (rowcontent, row) => {
            return localStorage.getItem('total_berat_brg_ketemu') === null || undefined ? 0 : parseFloat(localStorage.getItem('total_berat_brg_ketemu')).toFixed(3)
          },
        },
      ],
    };
    this.btnOpname = this.btnOpname.bind(this);
  }
  componentDidMount() {
    localStorage.setItem('total_berat_brg_ketemu',0)
    localStorage.setItem('total_berat_brg_hilang', 0)
    localStorage.setItem('total_qty_brg_ketemu', 0)
    localStorage.setItem('total_qty_brg_hilang', 0)
  }
  componentWillUnmount() {
      localStorage.setItem('total_berat_brg_ketemu',0)
      localStorage.setItem('total_berat_brg_hilang', 0)
      localStorage.setItem('total_qty_brg_ketemu', 0)
      localStorage.setItem('total_qty_brg_hilang', 0)
  }
  btnOpname(data) {
    // console.log(data);
    this.setState({
      isLoadingBakiOpname: true,
    });
    let hasil = {
      kode_baki: data.baki_opname,
    };

    // console.log(this.state.barangHilang.length);
    // if (this.state.barangHilang.length > 0) {
    //   ToastNotification("info", "Harap Kosongkan Data Terlebih Dahulu")
    //   this.setState({
    //     barangHilang : [],
    //     isLoadingBakiOpname: false,
    //   });
    //   return false
    // }
    postData("opname/by-kode-baki/", hasil)
      .then((res) => {
        ToastNotification("success", res.data.message);
        this.props.dispatch(reset("KosongKanDataOpname"));
      })
      .then(() => {
        this.setState({
          isLoadingBakiOpname: false,
        });
        this.props.dispatch(hideModal());
        this.lihatstockopname()
      })
      .catch((err) => {
        this.setState({
          isLoadingBakiOpname: false,
        });
        ToastNotification('info',err.response === undefined ? "Terjadi Kesalahan Saat Request Data" : err.response.data)
      });
  }
  btnKosongkanDataOpname() {
    this.props.dispatch(showModal());
    this.setState({
      FormModal: "KOSONGKANOPNAME",
    });
  }
  lanjutOpname() {
    this.props.dispatch(showModal());
    this.setState({
      FormModal: "LANJUTOPNAME",
    });
  }
  lihatstockopname() {
    this.setState({
      loadingLihatSTockOpname : true
    })
    getData('opname/get/hasil').then((res) => {
      this.setState({
        loadingLihatSTockOpname : false
      })

      res.data.forEach(element => {
        let total_berat_ketemu = element.barang_ketemu.map((list)=> list.berat).reduce((a,b)=>a+b,0)
        let total_berat_brg_hilang = element.barang_hilang.map((list) => list.berat).reduce((a, b) => a + b, 0)

        localStorage.setItem('total_berat_brg_ketemu',JSON.stringify(total_berat_ketemu))
        localStorage.setItem('total_berat_brg_hilang', JSON.stringify(total_berat_brg_hilang))
        
        localStorage.setItem('total_qty_brg_ketemu', JSON.stringify(element.barang_ketemu.length))
        localStorage.setItem('total_qty_brg_hilang', JSON.stringify(element.barang_hilang.length))
        

        this.setState({
          barangKetemu : element.barang_ketemu,
          barangHilang : element.barang_hilang,
        })
      });
    }).catch((err) => {
      this.setState({
        loadingLihatSTockOpname: false
      })
      ToastNotification('info',err.response === undefined ? "Terjadi Kesalahan Saat Request Data" : err.response.data)
    })
  }
  kosongkanDataBaki() {
    deleteData("opname/kosongkan")
      .then((res) => {
        ToastNotification("success", res.data.message);
        this.setState({
          barangKetemu : [],
          barangHilang : [],
        })
      })
      .catch((err) => {
        ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data");
      });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Stock Opname</Link>
          </li>
          <li className="breadcrumb-item active">Stock Opname</li>
        </ol>
        <h1 className="page-header">Stock Opname </h1>
        <Panel>
          <PanelHeader>Stock Opname</PanelHeader>
          <br />
          {/* Stock Opname */}
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadStockOpname
                  loadingLihatSTockOpname={this.state.loadingLihatSTockOpname}
                  lihatstockopname={() => this.lihatstockopname()}
                  lanjutOpname={() => this.lanjutOpname()}
                  btnKosongkanDataOpname={() => this.btnKosongkanDataOpname()}
                />
              </div>
              <div className="col-12">
                <br />
                <hr />
                List Barang Tidak Ketemu
                <hr/>
                <TabelLihatBarangTidakKetemu
                  data={this.state.barangHilang}
                  tbl_barang_tidak_ketemu={this.state.tbl_barang_tidak_ketemu}
                />
              </div>
              <div className="col-12">
              <hr />
                List Barang Ketemu
                <hr/>
                <TabelListBarangKetemu
                  data={this.state.barangKetemu}
                  tbl_barang_ketemu={this.state.tbl_barang_ketemu}
                />
              </div>
            </div>
          </div>
          <br />
          {/* End Tambah Stock Opname  */}
          <ModalGlobal
            size="L"
            title={
              this.state.FormModal === "KOSONGKANOPNAME"
                ? "Form Kosongkan Opname"
                : "Proses Opname"
            }
            content={
              this.state.FormModal === "KOSONGKANOPNAME" ? (
                <KosongKanDataOpname
                  isLoadingBakiOpname={this.state.isLoadingBakiOpname}
                  kosongkanDataBaki={(data)=>this.kosongkanDataBaki(data)}
                  onSubmit={(data) => this.btnOpname(data)}
                />
              ) : (
                <ProsesOpname
                // onSubmit={(data) => this.simpanPembayaran(data)}
                />
              )
            }
          />
        </Panel>
      </div>
    );
  }
}

FormStockOpname = reduxForm({
  form: "FormStockOpname",
  enableReinitialize: true,
})(FormStockOpname);
export default connect()(FormStockOpname);
