import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import Tabel from "../../../components/helpers/tabel";
class TabelLihatBarangTidakKetemu extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <Tabel
        keyField="kode_barcode"
        data={this.props.data}
        columns={this.props.tbl_barang_tidak_ketemu}
        empty={true}
        textEmpty="Data Barang Kosong"
      />
    );
  }
}

TabelLihatBarangTidakKetemu = reduxForm({
  form: "TabelLihatBarangTidakKetemu",
  enableReinitialize: true,
})(TabelLihatBarangTidakKetemu);
export default connect()(TabelLihatBarangTidakKetemu);

