import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getDataBaki, hideModal } from "../../actions/datamaster_action";
import { ReanderSelect } from "../../components/helpers/field";
import ValidasiOpname from "../../Validasi/StockOpname/ValidasiOpname";
class KosongKanDataOpname extends Component {
  constructor(props) {
    super(props);
    this.state = {
      batal: false,
      isLoadingKosongkanBaki: false,
    };
  }
  
  batal() {
    this.props.dispatch(hideModal());
  }
  componentDidMount() {
    this.props.dispatch(getDataBaki());
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-12">
            <button
              className="btn btn-primary btn-block"
              onClick={() => this.props.kosongkanDataBaki()}
              disabled={this.state.isLoadingKosongkanBaki}
              type="button"
            >
              {this.state.isLoadingKosongkanBaki ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Kosongkan
                  Data Opname
                </>
              ) : (
                "Kosongkan Data Opname"
              )}
            </button>
            <br />
          </div>

          <div className="col-12">
            <Field
              name="baki_opname"
              label="Baki Opname"
              placeholder="Pilih Baki Opname"
              options={this.props.getDataBaki
                .filter((list) => list.kode_baki !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_baki,
                    name: list.kode_baki + " - " + list.nama_baki,
                  };
                  return data;
                })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-12">
            <div className="row">
              <div className="col-6">
                <button
                  className="btn btn-primary btn-block"
                  disabled={this.props.isLoadingBakiOpname}
                  type="submit"
                >
                  {this.props.isLoadingBakiOpname ? (
                    <>
                      <i className="fas fa-spinner fa-spin"></i> &nbsp; Opname
                    </>
                  ) : (
                    "Opname"
                  )}
                </button>
              </div>
              <div className="col-6">
                <button
                  className="btn btn-primary btn-block"
                  disabled={this.state.batal}
                  type="button"
                  onClick={() => this.batal()}
                >
                  {this.state.batal ? (
                    <>
                      <i className="fas fa-spinner fa-spin"></i> &nbsp; Batal
                    </>
                  ) : (
                    "Batal"
                  )}
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

KosongKanDataOpname = reduxForm({
  form: "KosongKanDataOpname",
  enableReinitialize: true,
  validate: ValidasiOpname,
})(KosongKanDataOpname);
// export default connect()(KosongKanDataOpname);

export default connect((state) => {
  return {
    getDataBaki: state.datamaster.getDataBaki,
  };
})(KosongKanDataOpname);
